import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';
import { Platform, MenuController, NavController, LoadingController, AlertController } from '@ionic/angular';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Network } from '@ionic-native/network/ngx';
import { BaseService } from './services/base.service';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { BasePagePage } from './shared/base-page/base-page.page';
import { config, Constants } from './data-model/constant.model';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent extends BasePagePage implements OnInit, OnDestroy, AfterViewInit {

  public appPages = [
    {
      title: 'Operator',
      url: '/operator',
      icon: 'people'
    },
    {
      title: 'Usage',
      url: '/usage',
      icon: 'stats'
    },
    {
      title: 'Impression',
      url: '/impression',
      icon: 'bulb'
    },
    {
      title: 'WIP',
      // url: '/wip',
      url: '/wip/dept-selection',
      icon: 'briefcase'
    },
    {
      title: 'Machine Output',
      // url: '/output',
      url: '/output/dept-selection',
      icon: 'construct'
    },
    {
      title: 'Logout',
      url: '/logout',
      icon: 'log-out'
    }
  ];

  isConnectionLost: boolean = false

  backButtonSubscription; // for storing the returned subscription

  selectedPath = '';

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private platform: Platform,
    private statusBar: StatusBar,
    private baseService: BaseService,
    private network: Network,
    private router: Router,
    public navCtrl: NavController,
    public menuCtrl: MenuController
  ) {
    super(loadingCtrl, alertCtrl);
    this.initializeApp();
  }

  initializeApp() {

    this.router.events.subscribe((event: RouterEvent) => {
      if (event instanceof NavigationEnd) {

        this.selectedPath = event.url

        let arrPath = this.selectedPath.split('/');

        if (arrPath.length > 2) {

          this.backButtonSubscription = this.platform.backButton.subscribeWithPriority(1, () => {

            this.navCtrl.back();

          });

        } else {

          this.backButtonSubscription = this.platform.backButton.subscribeWithPriority(1, () => {

            BasePagePage.presentAlert('Alert', 'Are you sure want to exit app?', (res) => {

              if (res.role == Constants.b_OK) {

                navigator['app'].exitApp();
                
              }
  
            }, [Constants.b_CANCEL, Constants.b_OK]);

          });

        }

      }

    });

    this.platform.ready().then(() => {
      // this.statusBar.styleDefault();
      this.statusBar.styleLightContent();
    });

    // disable backbutton hardware event action
    // this.platform.ready().then(() => {
    //   this.platform.backButton.subscribeWithPriority(9999, () => {
    //     document.addEventListener('backbutton', function (event) {
    //       event.preventDefault();
    //       event.stopPropagation();
    //     }, false);
    //   });
    //   this.statusBar.styleDefault();
    // });

    // watch network for a connection
    let connectSubscription = this.network.onConnect().subscribe(() => {
      config.log('network connected!');
      // We just got a connection but we need to wait briefly
      // before we determine the connection type. Might need to wait.
      // prior to doing any api requests as well.
      if (this.isConnectionLost == true) {
        this.isConnectionLost = false
      }
      setTimeout(() => {
        if (this.network.type === 'wifi') {
          config.log('we got a wifi connection, woohoo!');
        }
      }, 3000);

    });

    // watch network for a disconnection
    let disconnectSubscription = this.network.onDisconnect().subscribe(() => {
      config.log('network was disconnected :-(');
      // this.baseService.presentAlert("Oppss..", "Network connection was lost. 😢");
      this.isConnectionLost = true
    });

  }

  ngOnInit() {
    // this.platform.ready().then(() => {
    //   navigator['app'].clearHistory();
    // });
  }

  ngAfterViewInit() {
    // this.subsribeHardwareBackButton();
  }

  ngOnDestroy() {
    // this.unsubsribeHardwareBackButton();
  }

  close() {
    config.log('CLOSE');
    this.menuCtrl.close('first');
  }

}
